.preloader-container {
    grid-area: view;
    justify-self: center;
    position: relative;
    width: 30rem;
    height: 30rem;  }

.preloader-container div {
    position: absolute;
    border: 4px solid #fff;
    opacity: 1;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    animation: preloader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite; }

.preloader-container div:nth-child(2) { animation-delay: -0.5s; }

@keyframes preloader {
  0% {
    top: 15rem;
    left: 15rem;
    width: 0;
    height: 0;
    opacity: 0; }

  4.9% {
    top: 15rem;
    left: 15rem;
    width: 0;
    height: 0;
    opacity: 0; }

  5% {
    top: 15rem;
    left: 15rem;
    width: 0;
    height: 0;
    opacity: 1; }

  100% {
    top: 0px;
    left: 0px;
    width: 30rem;
    height: 30rem;
    opacity: 0; } }